export default {
	name: "tuiTips",
	props: {
		//top bottom center
		position: {
			type: String,
			default: "top"
		}
	},
	data() {
		return {
			timer: null,
			show: false,
			msg: "无法连接到服务器~",
			//translucent,primary,green,warning,danger
			type: "translucent"
		};
	},
	methods: {
		showTips: function(options) {
			const {
				type = 'translucent',
					duration = 2000
			} = options;
			clearTimeout(this.timer);
			this.show = true;
			// this.duration = duration < 2000 ? 2000 : duration;
			this.type = type;
			this.msg = options.msg;
			this.timer = setTimeout(() => {
				this.show = false;
				clearTimeout(this.timer);
				this.timer = null;
			}, duration);
		}
	}
}
